.container {
  margin: 0 auto;
  padding: 0 20px;

  @media screen and (min-width: 320px) {
    width: 320px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 40px;
  }
  @media screen and (min-width: 1440px) {
    width: 1440px;
    padding: 0 84px;
  }
}
